.Resume{
	width: 22cm;
		display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	overflow: scroll;
	row-gap: 1cm;

/*	height: 50vh;*/

}

.headR{
	width: 22cm;
	height: 7vh;
	display: flex;
	align-items: center;
	justify-content: center;


}
.headB{
	width: 200px;
	height: 50px;
	border: none;
	border-radius: 8px;
	background-color: #5fbff9;
	box-shadow: 1px 1px 3px 2px rgb(0 0 0 / 10%);
	font-size: 23px;
	cursor: pointer;

}
#bodyR{
	
	margin-bottom: 2vh;
/*	border: 1px 	solid;*/
     width: 21cm;
     height: 29.7cm;
/*	 overflow: scroll;*/
	 background-color: white;
	 border: none;
	 box-shadow: 1px 1px 3px 2px rgb(0 0 0 / 10%);
	 display: grid;
	 grid-template-rows: 4cm 8cm 18.7cm;
}
@page {
  size: A4;
  margin: 0;
}

@media print {
  .page {
    margin: 0;
    border: initial;
    border-radius: initial;
    width: initial;
    min-height: initial;
    box-shadow: initial;
    background: initial;
    page-break-after: always;
  }
}


