.NavEditor{
	width: 15cm;
	height: 9vh;
	background-color: #BED784;

}


.overlayEditor {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color:#C5C6D3;
/*  background-color: rgba(0,0,0, 0.9);*/
  overflow-y: hidden;
  transition: 0.5s;
}


.overlay-contentEditor {
  position: relative;
  top: 15%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlayEditor a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: black;
  display: block;
  transition: 0.3s;
}

.overlayEditor a:hover, .overlayEditor a:focus {
  color: #f1f1f1;
}

.overlayEditor .closebtn {
  position: absolute;
  top: 10px;
  right: 45px;
  font-size: 60px;
}


.NavMenu{
	display: flex;
	column-gap: 2vw;
	padding-left: 1vw;
	font-size: 20px;
	align-items: center;
	cursor: pointer;
}

@media screen and (max-width: 900px){
	#menuEx{
		display: none;
	}
	#menuEd{
		display: none;
	}
	#menuP{
		display: none;
	}
	#menuS{
		display: none;
	}
	#menuB{
		display: none;
	}
	.NavMenu{
		display: flex;
		align-content: center;
		justify-content: space-between;
	}
		.overlayEditor a{
      font-size: 30px;
    }
	
	.medit{
		margin-top: 3vh;
		padding-right: 2vw;
	}
}

@media screen and (max-width: 650px){
		.NavEditor{
			width: 13cm;
		}

}
@media screen and (max-width: 550px){
		.NavEditor{
			width: 11cm;
		}
		.overlayEditor a{
      font-size: 20px;
    }

}

@media screen and (max-width: 450px){
		.NavEditor{
			width: 9cm;
		}

}

@media screen and (max-width: 370px){
		.NavEditor{
			width: 7cm;
		}

}