.Home{
/*	border-bottom: 0.6px solid lightgray;*/
/*	padding-bottom: 3%;*/
	width: 100vw;
	height: 86vh;
	background-color: #f9dec9;
	overflow: scroll;
}

.container{
	height: 80%;
    width: 80vw;
    margin-left: 10vw;
    text-align: left;
    padding-bottom: 2vh;



}





.bodyTextH{

	font-family: monospace;
	font-size: 40px;

	overflow: hidden;
	white-space: nowrap;
	
	margin: 0px 10px;
	padding-top: 5vh;
	animation: typing 3.5s steps(40, end);
	
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
.bodyText{
/*	font-family: monospace;*/


	overflow: hidden;
	
	font-size: 25px;
	margin:10px 10px;
	
}
.build{
	font-size: 25px;
	border-radius: 8px;
	border: none;
	height: 60px;
	width: 250px;
	background-color: #5fbff9;
	cursor: pointer;
	color: black;

}



@media screen and (max-width: 900px){
	.bodyTextH{
		font-size: 35px;
	}
	.bodyText{
		font-size: 23px;
	}
}
@media screen and (max-width: 600px){
	.bodyTextH{
