.Templates{
	height: 86vh;
	background-color: #f9dec9;
}
.containerTemp{
	width: 80vw;
	margin-left: 10vw;
/*	padding-top: 4vh;*/
	padding-bottom: 3vh;
	height: 80vh;
	overflow: scroll;
}
.hText b{
	font-size: 28px;
	margin: 20px 5px;
	font-family: monospace;
}
.TemplateCards{
	padding-top: 7vh;
	padding-bottom: 3vh;
	display: flex;
	flex-wrap: wrap;
	column-gap: 5vw;
	row-gap: 8vh;
	justify-content: center;
	overflow: scroll;

}
.templateType{
	width: 380px;
	height: 200px;
	border: solid .5px black;
	background-color: #c9d996;
	border-radius: 10px;
	box-shadow: 4px 4px 2px 2px black;
}
.heading{
	margin: 0px 0px;
	text-align: center;
	font-size: 30px;
	border-bottom: 0.5px solid black;
}
.templateBody{
	font-size: 23px;
	text-align: center;
	margin: 18px 0px;
}
.explore{
	font-size: 25px;
	border: none;
	font-weight: bold;
	background-color: #5fbff9;
	width: 300px;
	border-radius: 8px;
	height: 50px;
	cursor: pointer;

}
.buttonT{
	text-align: center;
	padding-bottom: 1vh;
}

@media screen and (max-width:900px){
	.hText b{
	font-size: 26px;
}
}

@media screen and (max-width: 500px){

	.templateType{
		width: 300px;
		height: 170px;
	}
	.hText b{
	font-size: 22px;
	}
	.explore{
		height: 35px;
		width: 250px;
	}
	.templateBody{
		font-size: 18px;
	}
	.heading{
		font-size: 27px;
	}

}

@media screen and (max-width: 400px){

	.hText b{
	font-size: 20px;
	}

	.templateType{
		width: 260px;
		height: 170px;
	}
	.explore{
		height: 35px;
		width: 220px;
	}
	.templateBody{
		font-size: 18px;
	}
	.heading{
		font-size: 25px;
	}

}
@media screen and (max-width: 350px){

	.hText b{
	font-size: 18px;
}

	.templateType{
		width: 200px;
		height: 150px;
	}
	.explore{
		height: 30px;
		width: 170px;
	}
	.templateBody{
		font-size: 16px;
	}
	.heading{
		font-size: 21px;
	}

}

@media screen and (max-width: 268px){

	.hText b{
	font-size: 15px;
}

	.templateType{
		width: 170px;
		height: 150px;
	}
	.explore{
		height: 27px;
		width: 140px;
	}
	.templateBody{
		font-size: 15px;
	}
	.heading{
		font-size: 17px;
	}

}

@media screen and (max-width: 225px){

	.hText b{
	font-size: 14px;
}

	.templateType{
		width: 150px;
		height: 130px;
	}
	.explore{
		height: 27px;
		width: 110px;
	}
	.templateBody{
		font-size: 13px;
	}
	.heading{
		font-size: 14px;
	}

}

