.Basic{
	overflow: scroll;
	height: 12cm;
}
form{
	padding-left: 60px;
	padding-top: 4vh;
	padding-right: 40px;
	padding-bottom: 4vh;

}
#formHeading{
	font-size: 22px;
	margin: 5px 0px;

}
.formInput{
	width: 10cm;
	height: 1.1cm;
	font-size: 20px;
	border: none;
	border-radius: 7px;
	 box-shadow: 1px 1px 3px 2px rgb(0 0 0 / 10%);
}
.deleteField{

	height: 4vh;
	cursor: pointer;
}

.dimg{
	display: flex;
	justify-content: space-between;
}

@media screen and (max-width: 550px){
		.formInput{
			width: 8cm;
		}
		.Basic{
			height: 11.3cm;
		}

}

@media screen and (max-width: 450px){
		form{
			padding-left: 40px;
		}

		.formInput{
			width: 7cm;
			font-size: 18px;
		}

		#formHeading{
			font-size: 20px;
			margin: 5px 0px;
		}

}
@media screen and (max-width: 370px){
		.formInput{
			width: 6cm;
			height: 1cm;
		}
		form{
			padding-left: 20px;
		}

}