.NavBar{
	color: black;
	width: 100vw;
	display: flex;
	height: 9vh;
/*	border-bottom: 0.5px solid lightgray;*/
	background-color: #89a9d2;

}

.overlay {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color:#adadad;
/*  background-color: rgba(0,0,0, 0.9);*/
  overflow-y: hidden;
  transition: 0.5s;
}
.logoPic{
		width: 60px;
		height: 47px;
		margin-top: 7px;
}

.overlay-content {
  position: relative;
  top: 15%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: black;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 10px;
  right: 45px;
  font-size: 60px;
}

.m{
  height: 3vh;
  display: none;
  cursor: pointer;
}
.menu{
	display: flex;
	width: 50vw;
	column-gap: 2vw;
	align-items: center;
	justify-content: center;
}
.logo{
	width: 42vw;
	display: flex;
	justify-content: flex-start;
	margin-left: 5vw;
	cursor: pointer;
	column-gap: 0.5vw;
/*	margin-top: -0.5vw;*/
	align-items: center;

}
.logoT{
	color: black;
	font-size: 25px;


}
.menuItem{
	font-size: 20px;
	font-family: monospace;
	cursor: pointer; 
	color: black;

}

.menuItem:hover{
	text-decoration: underline;
}

.coffee{
	height: 30px;
}
@media screen and (max-width: 900px){
	.m{
		display: inline;

	}
	.menuItem{
		display: none;
	}
	.menu{
		justify-content: flex-end;
		width: 20vw;
		padding-right: 2vw;
		column-gap: 4vw;
	}
	.logo{
		width: 65vw;
	}

	.overlay a{
      font-size: 30px;
    }
    .coffee{
    	height: 30px;
    }
}

@media screen and (max-width: 500px){
  
    .overlay a{
      font-size: 20px;
    }
    .logoT{
    	display: none;
    }
}
@media screen and (max-width: 350px){
	
	.coffee{
    	height: 30px;
    }
}
@media screen and (max-width: 300px){
	
	.coffee{
    	height: 25px;
    }
}
