.Editor{
	width: 15cm;
	height: 14cm;
	background-color: #E3DFFF;
	
	box-shadow: 1px 1px 3px 2px rgb(0 0 0 / 10%)

}

.profileOneS{
	height: 20vh;
}

@media screen and (max-width: 650px){
	.Editor{
		width: 13cm;
	}
}

@media screen and (max-width: 550px){
	.Editor{
		width: 11cm;
	}
}

@media screen and (max-width: 450px){
		.Editor{
			width: 9cm;
		}

}

@media screen and (max-width: 370px){
		.Editor{
			width: 7cm;
		}

}

